.drop-btn {
  width: auto;
  min-width: 50px;
  min-height: 50px;
  padding: 1px 5px;
  background-color: transparent;
  color: var(--white);
}

.drop-btn:hover {
  background-color: var(--babyBlue);
  color: var(--backgroundGreen);
}

.drop-btn:hover .drop-line {
  background-color: var(--backgroundGreen);
}

.drop-links {
  display: flex;
  flex-direction: column;
  margin: 5px 20px;
  background-color: var(--pink);
}

.drop-links a {
  color: var(--backgroundGreen);
  text-decoration: none;
  padding: 5px 10px;
}

.drop-links a:hover {
  background-color: var(--backgroundGreen);
  color: white;
}

.drop-line {
  background-color: white;
  height: 2px;
  border: none;
  border-radius: 3px;
}

.drop-nav {
  padding: 5px;
  display: none;
}

.header {
  margin: 20px 0px 0px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 25px;
  border-radius: 50px;
}

.links {
  display: flex;
  justify-content: space-between;
  width: 200px;
}

.links a {
  text-decoration: none;
  color: var(--white);
  padding: 5px 10px;
  border-radius: 5px;
  color: var(--white);
}

.links a:hover {
  background-color: var(--pink);
  color: var(--backgroundGreen);
}

.links .active {
  color: var(--purple);
}

.x-btn {
  font-size: 30px;
}

@media (max-width: 1400px) {
  .drop-nav {
    display: block;
  }

  nav {
    display: none;
  }
}
