.about {
  padding: 50px;
  background-color: var(--babyBlue);
  margin: 30px 0px;
  border-radius: 50px;
  max-width: 100%;
}

.about h2 {
  position: absolute;
  left: 40%;
  color: white;
  background-image: linear-gradient(45deg, var(--brightGreenBlue), var(--pink));
  padding: 5px 8px;
  border-radius: 10px;
  border: 5px solid var(--babyBlue);
}

.about-divs {
  display: grid;
  grid-template-columns: repeat(auto-fit, 25rem 30rem);
  align-items: center;
  justify-content: center;
}

.about-left {
  max-width: 100%;
}

.about-right {
  margin-top: 100px;
  margin-left: -30px;
  background-color: var(--babyBlue);
  padding: 5px 15px;
  border: 8px solid var(--brightGreenBlue);
  border-radius: 15px;
}

.about-right p {
  color: var(--black);
}

.left-img {
  width: auto;
  height: auto;
  max-width: 100%;
  border: 8px solid var(--purple);
  border-bottom: 25px solid var(--purple);
  border-radius: 15px;
}

@media (max-width: 1100px) {
  .about-divs {
    grid-template-columns: repeat(auto-fit, 20rem 25rem);
  }
}

@media (max-width: 900px) {
  .about-divs {
    grid-template-columns: repeat(auto-fit, 30rem);
  }

  .about-right {
    margin-top: -200px;
    margin-right: 100px;
  }
}

@media (max-width: 650px) {
  .about {
    padding: 50px 5px;
  }
  .about-divs {
    grid-template-columns: repeat(auto-fit, 20rem);
  }

  .about-right {
    margin-top: -100px;
    margin-right: 0;
  }
}
