.line-one {
  height: 3px;
  border: none;
  width: 50%;
  background-image: linear-gradient(90deg, var(--babyBlue), var(--purple));
}

.skills {
  padding: 20px 0px;
}

.skill-item {
  text-align: center;
  min-width: 100px;
  border-radius: 20px;
  color: var(--babyBlue);
  background-color: var(--backgroundGreen);
  margin: 2px;
}

.skill-item h5 {
  margin: 0;
  padding: 10px 0;
}

.skill-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 8rem);
  justify-content: center;
}
