.feed-img-container {
  max-width: 90%;
  position: relative;
  margin-top: -60px;
  border: 9px solid var(--babyBlue);
  border-bottom: 15px solid var(--babyBlue);
  background-color: var(--babyBlue);
  border-radius: 5px;
}

.home-feed-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 20rem);
  justify-content: center;
}

.homeFeed-block {
  margin: 0 10px;
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--backgroundGreen);
  border-radius: 20px;
  margin-top: 80px;
}

.homeFeed-img {
  max-width: 100%;
}

.project-home-feed {
  margin-top: 20px;
  padding: 5px 40px;
}

.project-home-feed h2 {
  color: white;
  background-image: linear-gradient(45deg, var(--brightGreenBlue), var(--pink));
  padding: 5px 15px;
  border-radius: 10px;
  text-align: center;
  text-transform: uppercase;
  max-width: fit-content;
}

.project-text .btn-wrapper {
  margin-bottom: 10px;
}

@media (max-width: 1190px) {
  .home-feed-grid {
    grid-template-columns: repeat(auto-fit, 15rem);
  }
}

@media (max-width: 920px) {
  .project-home-feed {
    padding: 5px 0px;
  }
}

@media (max-width: 550px) {
  .home-feed-grid {
    grid-template-columns: repeat(auto-fit, 20rem);
  }
}
