.btn-wrapper {
  max-width: fit-content;
  padding: 3px;
  border-radius: 13px;
  background-image: linear-gradient(45deg, var(--brightGreenBlue), var(--pink));
}

.design-guy {
  max-width: 100%;
  width: auto;
  height: auto;
}

.descriptive-text {
  padding: 0 20px 15px 20px;
}

.hero {
  display: grid;
  grid-template-columns: repeat(auto-fit, 50%);
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  margin: 0px 0px 10px 0px;
  background-color: var(--backgroundGreen);
  padding: 0px 40px 20px 40px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}

.hero-img {
  margin-left: 15px;
  align-self: center;
}

.hero-text {
  margin-right: 5px;
  padding: 0px 90px 0px 0px;
}

.hero-btn {
  display: flex;
  justify-content: flex-end;
  gap: 5%;
  margin-left: 20px;
}

.main-text {
  background-color: var(--backgroundGreen);
  padding-left: 15px;
}

.main-text h1 {
  line-height: 1;
}

.main-text > p {
  margin-top: -20px;
}

.main-text-wrapper {
  padding: 0 0 0 5px;
  background-image: linear-gradient(
    180deg,
    var(--brightGreenBlue),
    var(--pink)
  );
}

.port-div {
  background-color: var(--backgroundGreen);
  border-radius: 50px;
}

.portfolio {
  max-width: 100%;
}

@media (max-width: 800px) {
  .descriptive-text {
    padding: 0 0px 15px 20px;
  }

  .hero-text {
    margin-right: 0;
    padding-right: 5px;
  }
}

@media (max-width: 600px) {
  .hero {
    grid-template-columns: repeat(auto-fit, 100%);
  }

  .hero-img {
    display: none;
  }
}
