.project-feed {
  margin-bottom: 20px;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 25rem);
  justify-content: center;
}

.project-header h2 {
  color: white;
  background-image: linear-gradient(45deg, var(--brightGreenBlue), var(--pink));
  padding: 5px 15px;
  border-radius: 10px;
  text-align: center;
  text-transform: uppercase;
  max-width: fit-content;
}

.project-text {
  max-width: 100%;
  overflow-wrap: break-word;
}
