.contact {
  background-color: var(--backgroundGreen);
  padding: 30px 40px;
  border-radius: 50px;
}

.contact h2 {
  color: white;
  background-image: linear-gradient(45deg, var(--brightGreenBlue), var(--pink));
  padding: 5px 15px;
  border-radius: 15px;
  border: 8px solid var(--backgroundGreen);
  text-align: center;
  text-transform: uppercase;
}

.contact-img {
  max-width: 80%;
  transform: rotate(0deg);
  border: 8px solid var(--brightGreenBlue);
  border-bottom: 20px solid var(--brightGreenBlue);
}

.contact-left {
  margin: 10px;
}
.contact-left form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.contact-right {
  background-image: url("../images/telephone.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  text-align: center;
}

.contact-sections {
  display: grid;
  grid-template-columns: repeat(auto-fit, 35rem);
  align-items: center;
  justify-content: center;
}

.contact-title {
  border-radius: 10px;
}

.default-text,
.success-text {
  background-color: var(--backgroundGreen);
  margin-top: 150px;
}

.input {
  border: none;
  height: 35px;
  text-align: center;
  width: 100%;
  max-width: 100%;
  color: white;
  background-color: var(--backgroundGreen);
}

.input-wrapper {
  background-image: linear-gradient(45deg, var(--brightGreenBlue), var(--pink));
  width: 80%;
  padding: 0 0px 1px 0px;
}

@media (max-width: 1350px) {
  .contact {
    padding-left: 10px;
    padding-right: 10px;
  }

  .contact-sections {
    grid-template-columns: repeat(auto-fit, 30rem);
  }
}

@media (max-width: 1200px) {
  .contact h2 {
    left: 65%;
  }

  .contact-sections {
    grid-template-columns: repeat(auto-fit, 20rem);
  }
}

@media (max-width: 718px) {
  .default-text,
  .success-text {
    margin-top: 0px;
  }

  .contact-right {
    background-image: none;
  }
}
