@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

:root {
  --purple: #7a73d1;
  --babyBlue: #e2f2fe;
  --brightGreenBlue: #74c9ae;
  --pink: #e267ec;
  --orange: #e78c90;
  --white: #ffffff;
  --backgroundGreen: #0a181b;
  --black: #071013;
}

a {
  color: white;
}

a:visited {
  color: white;
}

body {
  margin: 0;
  background-color: var(--black);
  padding: 0px 50px;
  font-family: "Poppins", sans-serif;
}

button {
  border: none;
  padding: 5px 10px;
  min-width: 100px;
  background-color: var(--babyBlue);
  border-radius: 10px;
  color: var(--backgroundGreen);
}

button:hover {
  background-color: transparent;
  color: white;
  cursor: pointer;
}

h1 {
  color: var(--orange);
  font-size: 4vw;
  font-weight: 600;
}

h2 {
  font-size: 2.1vw;
}

h3 {
  color: var(--orange);
  font-size: 2vw;
}

h4 {
  text-align: center;
  font-size: 2vw;
}

h5 {
  font-size: 1.5vw;
}

h6 {
  font-size: 2vw;
  color: var(--babyBlue);
}

p {
  color: white;
  font-size: 1.3vw;
}

small {
  color: var(--babyBlue);
}

@media (max-width: 1200px) {
  h1 {
    font-size: 35px;
  }

  h2 {
    font-size: 25px;
  }

  h3,
  h4 {
    font-size: 20px;
  }

  h5 {
    font-size: 15px;
  }

  h6 {
    font-size: 25px;
  }

  p {
    font-size: 18px;
  }
}

@media (max-width: 900px) {
  body {
    padding: 0 20px;
  }

  h1 {
    font-size: 35px;
  }

  h2 {
    font-size: 25px;
  }

  h3,
  h4 {
    font-size: 20px;
  }

  h5 {
    font-size: 15px;
  }

  h6 {
    font-size: 25px;
  }

  p {
    font-size: 18px;
  }
}
